@import "./reset.scss";

$메인컬러: #ff0000;

.red {
  color: $메인컬러;
}

.wow-enter {
  opacity: 0;
}

.wow-enter-active {
  opacity: 1;
  transition: all 1000ms;
}

@mixin 함수() {
  background: #eeeeee;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.my-alert {
  @include 함수();
}

.my-alert2 {
  @extend .my-alert;
  background: #fce8a9;
}

.my-alert p {
  margin-bottom: 0px;
}

.absol-box {
  background-color: #fce8a9;
  width: 200px;
  height: 300px;
  font-size: 9px;
  position: absolute;
  position: sticky;
  right: 0px;
  top: 0px;
}

